.Con{
    background-color: white;
    display: grid;
    grid-template-columns: 1fr 2fr 3fr 3fr 1fr;
    align-items: baseline;
    padding: 10px;
   
}

.brand_logo{
    width: 4rem;
    
}
.line{
    
    padding: 0px;
    margin: 0px;
    color: white;

}
.Logo_box{
    grid-column: 2/3;

}
.navigations p{
    padding: 0.2rem;
    color: black;
    font-weight: 500;
}

.navigations p:hover{
    padding: 0.2rem;
    color: black;
    font-weight: 500;
    text-decoration:underline;
    cursor: pointer;
}
.navigations{

    grid-column: 4/5;
    display: flex;
    justify-content: space-between;
}
@media (max-width: 404px) {
    .navigations p{
        font-size: 0.9rem;
    }
    .brand_logo{
        width: 3rem;
        
    }
}