.about_project div>video{
   width: 35rem;
}

.pro_sec_1,.pro_sec_2,.pro_sec_3{
    display: grid;
    grid-template-columns: 2fr 2fr ;
    margin-top:10rem;
    border: 0.5px solid grey;
    padding: 5rem;
}
.pro_sec_1{
    margin-top: 0rem;
}
.pro_sec_1>div:first-child,.pro_sec_2>div:first-child,.pro_sec_3>div:first-child{
grid-column: 1/2;
}
.pro_sec_1>div:nth-child(2),.pro_sec_2>div:nth-child(2),.pro_sec_3>div:nth-child(2){
grid-column: 2/3;
align-items: center;
    
}
.pro_sec_1>div:nth-child(2)>ul,.pro_sec_2>div:nth-child(2)>ul,.pro_sec_3>div:nth-child(2)>ul{
text-align: start;
margin-top:2rem;
        
    }

.project_view{
    background-color: rgb(152, 145, 145);
    color: white;
    border: none;
    padding: 0.3rem 0.6rem;

}
.project_repo{
    background-color: rgb(152, 145, 145);
    color: white;
    border: none;
    padding: 0.3rem 0.6rem;
    margin-left: 1rem;
}
.project_view>a,.project_repo>a{
    text-decoration: none;
    color: white;
}


    @media (max-width: 1140px) {
        .pro_sec_1,.pro_sec_2,.pro_sec_3{
            display: block;
            margin-top:7rem;
            padding: 2rem;
       
        }
    }

    @media (max-width: 600px) {
        .about_project div>video{
            width: 30rem;
       

         }
         .pro_sec_1,.pro_sec_2,.pro_sec_3{
      
            padding: 1rem;
       
        }
    }

    @media (max-width: 500px) {
        .about_project div>video{
            width: 23rem;
     

         }
         .pro_sec_1,.pro_sec_2,.pro_sec_3{
         border: none;
            padding: 0rem;
       
        }
    }