.about_profile{

    margin-top: 5rem;
   display: grid;
   grid-template-columns: 2fr 3fr;
   column-gap: 1rem;
   align-items: center;
}
.Image_con{
/* background-color: #c8c5c2; */
width: 30rem;
height: 20rem;
}
.profile{
    border:3px solid white;
    width: 100%;
    border-radius: 50%;
}
/* #c8c5c2 */
.resume_button{
    padding: 0.5rem 1.2rem;
    border-radius: 8px;
    border: none;
    background: black;
    color: white;
    font-weight: 800;
}
.profile_logo{
    width: 10%;
}
.profile_socile{
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding-top:1rem;
}

@media (max-width: 600px) {
    .about_profile{

        /* /* margin-top: 5rem; */
       display: grid; 
       grid-template-rows: 1fr 2fr;

       grid-template-columns: 1fr;
       /* column-gap: 1rem;
       align-items: center; */
    }
}

